*,
body {
    box-sizing: border-box;
}

form {
    display: block;
    text-align: center;
    width: 300px;
    margin: auto;
    margin-top: 15%;
    padding: 1.5%;
    background-color: #b8c6db;
    background-image: linear-gradient(315deg, #b8c6db 0%, #f5f7fa 74%);
    border-radius: 10px;
}

label,
.registerBtn {
    display: block;
    width: 200px;
    margin: auto;
    margin-top: 1%;
}